.main {
  padding: 1rem;
}

.main__title {
  margin-top: 0; 
}

.filters {
  display: flex;
  flex-wrap: wrap;
}

.filters__section {
  border: 1px solid #000;
  padding: 1rem;
  display: inline-block;
  margin-bottom: 1rem;
  margin-right: 1rem;
  width: 225px;
}

.filters__title {
  margin: 0;
}

.items__event {
  margin: 1rem 0;
  text-decoration: underline;
}

.items__checkbox {
  display: flex;
}

.items_checkbox-label {
  display: flex;
  flex-direction: row-reverse;
}

.items_checkbox-input {
  margin-right: .5rem;
}
